.gallery,
.library-gallery {
  display: grid;
  grid-auto-columns: 280px;
  grid-auto-rows: 443px;
  grid-row-gap: 20px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;

  @include tab {
    max-width: 768px;
    grid-template-columns: repeat(2, 336px);
    grid-auto-columns: 336px;
    grid-auto-rows: 495px;
    grid-column-gap: 32px;
    grid-row-gap: 32px;
  }

  @include desk {
    max-width: 1280px;
    grid-template-columns: repeat(3, 395px);
    grid-auto-columns: 395px;
    grid-auto-rows: 634px;
    grid-column-gap: 16px;
  }
}

.gallery__link {
  position: relative;
  cursor: default;
}

.gallery__image {
  height: 402px;
  width: 100%;
  object-fit: cover;
  border-radius: 10px 10px 5px 5px;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);

  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 500ms cubic-bezier(0.4, 0, 0.2, 1),
    border-color 500ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    transform: scale(1.03);
    box-shadow: 
      0 14px 28px rgba(0, 0, 0, 0.25), 
      0 10px 10px rgba(0, 0, 0, 0.22),
      0 0 0 1px rgba(255, 107, 8, 0.1);
    border-color: rgba(255, 107, 8, 0.3);
  }

  @include tab {
    height: 455px;
  }

  @include desk {
    height: 574px;
    width: 634px;
  }
}

.info {
  font-style: normal;
  font-weight: 500;
  padding: 8px 12px;
  height: 40px;
  background: transparent;
  border-radius: 0 0 5px 5px;
  margin-top: -5px;

  @include desk {
    height: 60px;
    padding: 12px 16px;
  }
}

.info__item {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-transform: uppercase;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  cursor: text;

  @include desk {
    font-size: 20px;
    line-height: 1.2;
  }
}

.info-detail {
  display: flex;
  justify-content: start;
  align-items: center;
}

.info-detail__item {
  font-size: 12px;
  line-height: 1.33;
  color: #ff6b08;
  margin-right: 5px;
  margin-top: 5px;
  cursor: text;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;

  @include desk {
    font-size: 20px;
    line-height: 1.2;
  }
}

.info-detail__item:first-child {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.info-detail__item:not(:first-child) {
  display: flex;
  justify-content: center;
  align-items: center;
}

.info-detail__item:not(:last-child) {
  border-right: 2px solid #ff6b08;
  padding-right: 5px;
}

.not-succesful-search-text {
  margin-top: 20px;

  @media screen and (min-width: 768px) {
    margin-top: 0;
    position: absolute;
    bottom: 35%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  text-align: center;
  letter-spacing: 0.02em;
  color: rgba(0, 0, 0, 0.4);
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 900;
  @media screen and (min-width: 768px) {
    font-size: 28px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 32px;
  }
}

// Light theme enhancements
body.light-theme {
  .info__item {
    color: #000000;
  }
  
  .info-detail__item {
    color: #ff6b08;
  }
}

// Dark theme enhancements
body.dark-theme {
  .gallery__image {
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.4);
    border: 1px solid rgba(255, 255, 255, 0.15);
    
    &:hover,
    &:focus {
      box-shadow: 
        0 20px 40px rgba(0, 0, 0, 0.5), 
        0 15px 20px rgba(0, 0, 0, 0.4),
        0 0 0 2px rgba(255, 107, 8, 0.3);
      border-color: rgba(255, 107, 8, 0.5);
    }
  }
  
  .info {
    background: transparent;
    box-shadow: none;
  }
  
  .info__item {
    color: #ffffff;
    text-shadow: 0 1px 3px rgba(0, 0, 0, 0.9);
    font-weight: 600;
  }
  
  .info-detail__item {
    color: #ff6b08;
    text-shadow: 0 1px 3px rgba(0, 0, 0, 0.9);
    font-weight: 600;
  }
}
