*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  font-family: system-ui, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  line-height: 1.15;
}

body {
  margin: 0;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  border-color: currentColor;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

body {
  font-family: Roboto, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.section {
  padding-top: 20px;
  padding-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

@media screen and (min-width: 768px) {
  .section.main {
    padding-top: 20px;
  }
}

.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.visually-hidden {
  opacity: 0;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  position: absolute;
}

#myBtn {
  z-index: 99;
  color: #fff;
  cursor: pointer;
  background-color: #ff6b08;
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 15px;
  font-size: 18px;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
}

#myBtn:hover {
  background-color: #555;
}

body {
  position: relative;
}

#auth-header {
  display: flex;
}

@media only screen and (max-width: 767px) {
  #auth-header {
    z-index: 5000;
    display: none;
    position: fixed;
    bottom: 5%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

@media screen and (min-width: 768px) {
  #auth-header {
    min-width: 175px;
    justify-content: end;
    margin-left: 20px;
  }
}

.fbs__nav-list {
  display: none;
}

@media screen and (max-width: 768px) {
  .fbs__nav-list {
    z-index: 999;
    position: absolute;
    top: 0;
    right: 0;
  }
}

@media screen and (min-width: 768px) {
  .fbs__nav-list {
    list-style: none;
    display: flex;
  }
}

.fbs__nav-item {
  position: relative;
}

.fbs__nav-item--active:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #ff6b08;
  position: absolute;
  bottom: 0;
  left: 0;
}

.fbs__nav-item:not(:last-child) {
  margin-right: 15px;
}

.fbs__nav-btn {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: none;
  margin: 0;
  padding: 7px 0 5px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.17;
  transition: color .3s cubic-bezier(.465, .183, .153, .946), border-color .3s cubic-bezier(.465, .183, .153, .946);
  position: relative;
}

@media screen and (max-width: 767px) {
  .fbs__nav-btn {
    font-size: 27px;
  }
}

.fbs__nav-btn:last-child {
  border: 1px solid #fff;
  border-radius: 5px;
  margin-left: 15px;
  padding: 5px 7px;
}

@media screen and (max-width: 767px) {
  .fbs__nav-btn:last-child {
    margin-top: 20px;
    margin-left: 0;
    padding: 7px 10px;
  }
}

.fbs__nav-btn.sign-up {
  padding-left: 23px;
}

.fbs__nav-btn:hover, .fbs__nav-btn:focus {
  color: #ff6b08;
  border-color: #ff6b08;
}

.fbs-header {
  max-width: 100%;
  z-index: 2;
  border: none;
  margin: 0 auto;
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translate(-50%);
}

@media screen and (min-width: 320px) {
  .fbs-header {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .fbs-header {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .fbs-header {
    width: 1280px;
  }
}

.fbs-header__section {
  justify-content: right;
  justify-items: center;
  display: flex;
}

.fbs-header-btn, .fbs-header-span {
  height: 20px;
  border: none;
  align-content: center;
  font-weight: 500;
}

.fbs-header-btn {
  width: 80px;
  cursor: pointer;
  color: #ff6b08;
  background-color: #f0ebe3;
  border-radius: 14px;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1);
}

.fbs-header-btn:first-child {
  margin-right: 20px;
}

.fbs-header-btn:hover, .fbs-header-btn:focus {
  color: wheat;
  background-color: #ff6b08;
  border: #ff6b08;
}

.fbs-header-span {
  color: wheat;
  margin-right: 20px;
}

.fbs-sign-menu {
  width: 100%;
  margin: 0 auto;
}

.fbs-mob-menu-close-btn {
  color: #f1ece2;
  fill: currentColor;
  background-color: #0000;
  border: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 30px;
  right: 20px;
}

.fbs-mob-menu-close-btn:hover, .fbs-mob-menu-close-btn:focus {
  fill: #f1ece2;
  background-color: #ff6b08;
}

.firebase-backdrop {
  height: 100%;
  margin: 0;
  padding: 0;
  position: relative;
}

.fbs-backdrop {
  z-index: 9;
  color: #fff;
  visibility: visible;
  opacity: 1;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(135deg, #000000e6 0%, #141414cc 100%);
  transition: opacity .35s ease-in, visibility .35s ease-in;
  position: fixed;
  top: 0;
  overflow-y: scroll;
}

@media screen and (max-width: 767px) {
  .fbs-backdrop {
    z-index: 9998;
  }
}

.fbs-modal {
  min-height: 480px;
  width: 340px;
  z-index: 9999;
  background: linear-gradient(145deg, #1a1a1a 0%, #2d2d2d 50%, #1a1a1a 100%);
  border-radius: 24px;
  padding: 35px 30px;
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 25px 50px #000c, 0 0 0 1px #ffffff1a, inset 0 1px #ffffff1a;
}

@media screen and (min-width: 768px) {
  .fbs-modal {
    width: 520px;
    min-height: 580px;
  }
}

@media screen and (max-width: 767px) {
  .fbs-modal {
    width: 300px;
    min-height: 450px;
    padding: 25px 20px;
  }
}

.fbs-modal:before {
  content: "";
  height: 4px;
  background: linear-gradient(90deg, #ff6b35, #f7931e, #ff6b35);
  border-radius: 24px 24px 0 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.fbs-modal-close-btn {
  width: 36px;
  height: 36px;
  cursor: pointer;
  color: #fff;
  backdrop-filter: blur(10px);
  background: #ffffff1a;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: all .3s;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.fbs-modal-close-btn:hover, .fbs-modal-close-btn:focus {
  background: #ff6b35cc;
  transform: scale(1.1);
  box-shadow: 0 0 20px #ff6b3566;
}

.fbs-modal-title {
  text-align: center;
  background: linear-gradient(135deg, #ff6b35, #f7931e);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -.5px;
  background-clip: text;
  margin-bottom: 25px;
  font-size: 28px;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .fbs-modal-title {
    margin-bottom: 30px;
    font-size: 36px;
  }
}

.fbs-modal-form {
  width: 100%;
  border-radius: 20px;
  margin: 0 auto;
  padding: 0;
  transition: opacity .3s, transform .3s;
}

.fbs-modal-form.hidden {
  opacity: 0;
  transform: scale(.95);
}

.fbs-modal-content {
  width: 100%;
  margin: 0 auto;
}

.fbs-modal-form-field {
  margin-bottom: 20px;
}

.fbs-modal-label {
  letter-spacing: .02em;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 35px;
  margin-bottom: 12px;
  margin-left: 0;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.125;
  display: block;
}

.fbs-modal-label--end {
  margin-bottom: 16px;
}

.fbs-modal-label__field {
  margin-top: 8px;
  margin-bottom: 0;
  display: flex;
  position: relative;
}

.fbs-modal-label__block {
  width: 100%;
  height: 56px;
  backdrop-filter: blur(10px);
  color: #fff;
  background: #ffffff0d;
  border: 2px solid #ffffff1a;
  border-radius: 16px;
  outline: none;
  padding: 0 20px 0 50px;
  font-size: 16px;
  font-weight: 500;
  transition: all .3s;
}

.fbs-modal-label__block::placeholder {
  letter-spacing: .01em;
  color: #fff9;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14286;
  transition: color .3s;
}

.fbs-modal-label__block:hover {
  background: #ffffff14;
  border-color: #ff6b3580;
}

.fbs-modal-label__block:focus {
  background: #ffffff1a;
  border-color: #ff6b35;
  box-shadow: 0 0 0 4px #ff6b351a;
}

.fbs-modal-label__block:hover::placeholder, .fbs-modal-label__block:focus::placeholder {
  color: #fffc;
}

.fbs-modal-label__block--input {
  height: 70px;
  margin: 10px;
  padding-left: 50px;
}

.fbs-modal-icon {
  fill: #fff9;
  width: 20px;
  height: 20px;
  transition: all .3s;
  position: absolute;
  top: 50%;
  left: 18px;
  transform: translateY(-50%);
}

.fbs-modal-label__block:hover + .fbs-modal-icon, .fbs-modal-label__block:focus + .fbs-modal-icon {
  fill: #ff6b35;
  transform: translateY(-50%)scale(1.1);
}

.fbs-modal-btn--submit {
  width: 100%;
  height: 56px;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #fff;
  background: linear-gradient(135deg, #ff6b35 0%, #f7931e 100%);
  border: none;
  border-radius: 16px;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 600;
  transition: all .3s;
  display: block;
  box-shadow: 0 8px 25px #ff6b354d;
}

.fbs-modal-btn--submit:hover, .fbs-modal-btn--submit:focus {
  background: linear-gradient(135deg, #f7931e 0%, #ff6b35 100%);
  transform: translateY(-2px);
  box-shadow: 0 12px 35px #ff6b3566;
}

.fbs-modal-btn--submit:active {
  transform: translateY(0);
  box-shadow: 0 6px 20px #ff6b354d;
}

.fbs-forgot {
  color: #ffffffb3;
  text-align: center;
  letter-spacing: .01em;
  margin-top: 15px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14286;
  text-decoration: none;
  transition: color .3s;
  display: block;
}

.fbs-forgot:hover, .fbs-forgot:focus {
  color: #ff6b35;
}

#logged-user {
  color: #fff;
}

#logout {
  color: #ff6b08;
  border-color: #ff6b08;
}

#logout:hover, #logout:focus {
  color: #fff;
  border-color: #fff;
}

@media screen and (max-width: 767px) {
  #logout {
    color: #fff;
    border-color: #fff;
    padding: 8px 12px;
  }

  #logout:hover, #logout:focus {
    color: #ff6b08;
    border-color: #ff6b08;
  }

  .login-status {
    text-align: center;
    flex-direction: column;
    padding: 10px;
    display: flex;
    position: relative;
  }
}

.login-status-icon {
  fill: currentColor;
  position: absolute;
  bottom: 14px;
  left: 2px;
}

@media screen and (min-width: 768px) {
  .login-status-icon {
    bottom: 7px;
    left: 2px;
  }
}

.policy-text {
  text-align: center;
  color: #757575;
  padding: 0 10px;
  position: fixed;
  bottom: 4%;
  left: 0;
}

.policy-link {
  color: #4286f4d2;
  text-decoration: none;
  transition: color .3s cubic-bezier(.465, .183, .153, .946);
}

.policy-link:hover, .policy-link:focus {
  color: #4285f4;
}

.fbs-success-message {
  text-align: center;
  min-height: 300px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
  display: flex;
  display: none !important;
}

.fbs-success-message.show {
  display: flex !important;
}

#logout-success {
  min-height: auto;
  backdrop-filter: blur(10px);
  z-index: 1000;
  white-space: nowrap;
  background: #000c;
  border: 1px solid #ffffff1a;
  border-radius: 12px;
  margin-top: 10px;
  padding: 15px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

#logout-success .fbs-success-icon {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, #4caf50, #45a049);
  margin-bottom: 10px;
  font-size: 20px;
}

#logout-success .fbs-success-text {
  background: linear-gradient(135deg, #4caf50, #45a049);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 8px;
  font-size: 16px;
}

#logout-success .fbs-success-subtext {
  color: #fffc;
  font-size: 12px;
}

#logout-success:before {
  content: "";
  width: 0;
  height: 0;
  border-bottom: 8px solid #000c;
  border-left: 8px solid #0000;
  border-right: 8px solid #0000;
  position: absolute;
  top: -8px;
  left: 50%;
  transform: translateX(-50%);
}

.fbs-success-icon {
  width: 80px;
  height: 80px;
  color: #fff;
  background: linear-gradient(135deg, #4caf50, #45a049);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 auto 25px;
  font-size: 40px;
  font-weight: bold;
  animation: successPulse .6s ease-in-out;
  display: flex;
  box-shadow: 0 8px 25px #4caf504d;
}

.fbs-success-text {
  color: #fff;
  background: linear-gradient(135deg, #4caf50, #45a049);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  background-clip: text;
  margin-bottom: 15px;
  font-size: 24px;
  font-weight: 600;
  animation: fadeInUp .8s ease-out .2s both;
}

.fbs-success-subtext {
  color: #fffc;
  text-align: center;
  max-width: 280px;
  font-size: 16px;
  line-height: 1.5;
  animation: fadeInUp .8s ease-out .4s both;
}

@keyframes successPulse {
  0% {
    opacity: 0;
    transform: scale(.8);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.login-required-notification {
  color: #fff;
  z-index: 10000;
  max-width: 320px;
  backdrop-filter: blur(10px);
  background: linear-gradient(135deg, #ff6b35, #f7931e);
  border: 1px solid #ffffff1a;
  border-radius: 16px;
  padding: 20px;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 20px;
  right: 20px;
  transform: translateX(400px);
  box-shadow: 0 12px 35px #ff6b3566;
}

.login-required-notification.show {
  transform: translateX(0);
}

.login-required-notification .notification-content {
  text-align: center;
}

.login-required-notification .notification-content p {
  margin: 0 0 15px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.4;
}

.login-required-notification .notification-login-btn {
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .5px;
  background: #fff3;
  border: 1px solid #ffffff4d;
  border-radius: 12px;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 600;
  transition: all .2s;
}

.login-required-notification .notification-login-btn:hover, .login-required-notification .notification-login-btn:focus {
  background: #ffffff4d;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px #fff3;
}

.login-required-notification .notification-login-btn:active {
  transform: translateY(0);
}

@media screen and (max-width: 767px) {
  .login-required-notification {
    max-width: none;
    top: 10px;
    left: 10px;
    right: 10px;
    transform: translateY(-100px);
  }

  .login-required-notification.show {
    transform: translateY(0);
  }
}

.backdrop-loader {
  z-index: 10000;
  visibility: visible;
  opacity: 1;
  width: 100vw;
  height: 100vh;
  background-color: #00000043;
  transition: opacity .25s linear, visibility .25s linear, background-color .25s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-loader--is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
}

.loader {
  z-index: 10001;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.lds-roller {
  width: 80px;
  height: 80px;
  display: inline-block;
  position: relative;
}

.is-hidden {
  display: none;
}

.lds-roller div {
  transform-origin: 40px 40px;
  animation: lds-roller 1.2s cubic-bezier(.5, 0, .5, 1) infinite;
}

.lds-roller div:after {
  content: " ";
  width: 8px;
  height: 8px;
  background: #fff;
  border-radius: 50%;
  margin: -4px 0 0 -4px;
  display: block;
  position: absolute;
}

.lds-roller div:nth-child(1) {
  animation-delay: -.036s;
}

.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}

.lds-roller div:nth-child(2) {
  animation-delay: -.072s;
}

.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}

.lds-roller div:nth-child(3) {
  animation-delay: -.108s;
}

.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}

.lds-roller div:nth-child(4) {
  animation-delay: -.144s;
}

.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}

.lds-roller div:nth-child(5) {
  animation-delay: -.18s;
}

.lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}

.lds-roller div:nth-child(6) {
  animation-delay: -.216s;
}

.lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}

.lds-roller div:nth-child(7) {
  animation-delay: -.252s;
}

.lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}

.lds-roller div:nth-child(8) {
  animation-delay: -.288s;
}

.lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
}

@keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
